.sidebar_option {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    cursor: pointer;
    color: #818181;
}

.sidebar_option > .MuiSvgIcon-root {
    padding: 5px;
}

.sidebar_option > h3 {
    flex: 1;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 400;
}

.sidebar_option > p {
    display: none;
    font-weight: 300;
}

.sidebar_option:hover,
.sidebar_option:hover > h3,
.sidebar_option:hover > p,
.active,
.active > h3,
.active > p {
    background-color: #fcecec !important ;
    color: #c04b37;
    font-weight: 800 !important ;
}

.sidebar_option:hover > p {
    display: inline;
}

.active > p {
    display: inline;
}