.mail{
    flex: 1;
    background-color: whitesmoke;
}

.mail__tools{
    display: flex;
    justify-content: space-between;
    background-color: white;
}

.mail__tools__left{
    display: flex;
}

.mail__body{
    display: flex;
    flex-direction: column;
    margin: 30px;
    background-color: white;
    padding: 20px;
    height: 100vh;
    box-shadow: 0 5px 7px 0 rgba(0,0,0,0.24);
}

.mail__body__header{
    display: flex;
    align-items: center;
    border-bottom: 1px solid whitesmoke;
    padding: 20px;
    position: relative;
}

.mail__body__header > h2 {
    font-weight: 400;
    margin-right: 20px;
}

.mail__body__header > p {
    margin-left: 20px;
}

.mail__body__header > p:last-child {
    position: absolute;
    top: 24px;
    right: 0;
    font-size: 12px;
    color: gray;
}

.mail__important{
    color: #e8ab02;
}

.mail__body__message > p {
    padding: 10px;
    margin-right: 20px;
    overflow-wrap: break-word;
}